<template>
  <div id="app">
    <h1 id="title">GIPHY API</h1>
    <p id="author">by Hasan Hüseyin Demir</p>
    <search v-on:searchrequested="handleSearch"></search>
    
    <preview :gifs="gifs"></preview> 
    
  </div>
</template>

<script>
import search from '../components/search.vue'
import preview from '../components/preview.vue'

export default {
  components: { search,preview },
  data(){
    return{
      gifs:[],
    }
  },
  methods:{
    handleSearch(query){
      this.gifs=[],
      fetch(`https://api.giphy.com/v1/gifs/search?q=${query}&api_key=9orlG4CfCPzBamUV6aN9SUFXkIYc0poX&limit=21&rating=g`)
      .then((res)=>{return res.json()})
      .then((res)=>{this.gifs=res.data});
    }
  },
  created(){
    fetch("https://api.giphy.com/v1/gifs/trending?api_key=9orlG4CfCPzBamUV6aN9SUFXkIYc0poX&limit=21&rating=g")
    .then((res)=>{return res.json()})
    .then((res)=>{this.gifs=res.data});
  },

}
</script>

<style>
*{
  transition: .3s;
}
#title{
  text-align: center;
  text-shadow: 3px 3px 3px rgba(0, 0, 0, 0.317);
  transition:.3s;
}
#title:hover{
  color:rgb(97, 97, 97);
}
#author{
  margin-top:-15px;
  font-size:8px;
  text-align:center;
}
body {
  font-family: 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  background-color: rgb(199, 199, 199);
}
#container{
  background-color: rgb(236, 236, 236);
  border-color: black;
  display: flex;
  padding: 5px;
}
</style>



