<template>
<div class="search">
    <input type="text" v-model="query" @keypress.enter="handleSearch" placeholder="Gif Ara...">
    <button @click="handleSearch">Ara</button>
</div>
</template>

<script>
export default{
    data(){
        return{
            query:'',
        }
    },
    methods:{
        handleSearch(){
            this.$emit("searchrequested",this.query)
        }
    }
}
</script>

<style scoped>
.search{
    background-color:rgba(70, 70, 70, 0.365);
    padding:5px;
}
.search input{
    padding:10px 20px;
    border:1px solid #CCC;
    border-radius: 5px;
    width:400px;
    align-content: center;
    text-align: center;
    position: relative;
    display: block;
    margin : 0 auto;
    font-weight: bold;
    font-size:24px;
    color:rgb(95, 95, 95)
}
.search input::placeholder{
    text-align: center;
}

.search button{
    padding:5px 15px;
    background: rgb(255, 119, 0);
    width:150px;
    height:40px;
    border:none;
    font-size:16px;
    border-radius: 2px;
    color:black;
    align-content: center;

    position: relative;
             display: block;
             margin : 0 auto;
}
.search button:hover{
    box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.148);
    background-color:orange;
}
</style>


