<template>
    <div id="container">
        <h3 v-if="gifs.length<1" id="loading">Gifler Aranıyor...</h3>
        <ul>
            <li v-for="gif in gifs">
                <img :src="gif.images.fixed_height_small.url" class="image">
                

            </li>
        </ul>
    </div>
</template>

<script>
export default{
    props:["gifs"],

}
</script>

<style>
h3{
    transition: .5s;
}
ul{
    display: flex;
    flex-wrap: wrap;
    overflow:hidden;
    justify-content: flex-start;    
}
li{
    list-style: none;
    float: left;
    margin:10px;
    transition:.4s;
    box-shadow: 0;
    overflow:hidden;
}
img{
    position:block;
}

.image{
    width:25vh;
    height: 25vh;
}

</style>


